import React from 'react';
import logo from './logo.svg';
import './App.css';

const log = str => {
  var d = new Date()
  console.log(d.toLocaleTimeString() + ' ' + d.getMilliseconds() + ":" + str)
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      token: "",      // 授信凭证
      vtDeviceId: "", // 虚拟设备编号
      queryTime: "",  // 授信查询时间
      fundList: ['3'],// 账户类型列表
      url: 'https://m.baidu.com/',
      url2: 'dfyj://dl/openaccount',
      clickedText: '',
    };
    log('react constructor');

    this.oauth = this.oauth.bind(this);
    this.login = this.login.bind(this);
    this.loginEnhance = this.loginEnhance.bind(this);
    this.selected = this.selected.bind(this);
    this.share = this.share.bind(this);
    this.navigateTo = this.navigateTo.bind(this);
    this.navigateNativeTo = this.navigateNativeTo.bind(this);
    this.navigateBar = this.navigateBar.bind(this);
  }

  componentDidMount() {
    log('react componentDidMount');

    /**
     * 初始化桥接程序
     */
    /* global fastman */
    fastman.ready(_=>{
      const _this = this;

      // 所有的桥接接口都必须在ready接口执行成功后才可被调用，如果需要在页面加载时就调用相关桥接接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
      fastman.oauth({
        success: function (res) {
          _this.setState({...res, queryTime: new Date().toLocaleTimeString() + ' ' + new Date().getMilliseconds()});
        },
        fail: function (err) {
          log('ready failure:' + JSON.stringify(err))
        }
      });
    });
  }

  componentWillUnmount() {
    log('react componentWillUnmount');
  }

  /**
    * 执行oauth指令
    * 获取设备编号及会话token
    * 设备编号：每个移动设备都会产生对应的标识符，用于区分不同的移动设备
    * 会话token：则是维持服务端服务之间的会话状态，以上2个参数在oauth指令成功回调可获取
   */
  oauth() {
    const _this = this;
    fastman.oauth({
      success: function (res) {
        _this.setState({...res, queryTime: new Date().toLocaleTimeString() + ' ' + new Date().getMilliseconds()});
      },
      fail: function (err) {
        log('oauth failure:' + JSON.stringify(err))
      }
    });
  }

  /**
    * 唤起登录
   */
  login() {
    fastman.onLogin()
  }

  /**
    * [加强版登录]
   */
  loginEnhance() {
    var payload = {
      type:'1',
    }
    if(this.state.fundList.length > 0){
      payload.fundtype = this.state.fundList.join(',');
    }
    payload.success = function(res) {
      if(res.isLogin == true){
        if(res.bizSysId == 3)
          log('普通资金账号')
        else if(res.bizSysId == 6)
          log('两融账号')
        else if(res.bizSysId == 18)
          log('期权账户')
      }
    }
    fastman.onLoginEnhance(payload)
  }

  /**
   * 分享
   */
  share(channel) {
    fastman.onShare({
      channel : channel,
      title : '东方赢家财富版上线啦',
      description : '东方赢家财富版是一款综合类金融工具APP',
      thumbImageUrl : 'http://wxs.dfzq.com.cn/fileRes/new_download/images/new_pic.jpg',
      link : 'http://wxs.dfzq.com.cn/fileRes/new_download/app_open/appOpen.html',
    })
  }

  /**
   * 导航前进(H5)
   */
  navigateTo() {
    var url = this.state.url
    if (url == null || url == ''){
        log('请填写正确的h5 url')
    } else {
        if (window.localStorage) {
            var storage = window.localStorage
            storage['url'] = url
        }

        fastman.onNavigateTo({
            title: '跳转地址标题',
            uri: url,
        })
    }
  }

  navigateBack() {
    fastman.onNavigateBack({
      success: function (resp) {
          log('返回成功')
      }
    })
  }

  /**
   * 第三方调用 - 模拟成功回调
   */
  reqOK() {
    fastman.onReq('IF060002', {
      days: '2',
      success : function (resp) {

      },
      fail : function(err) {

      },
    })
  }

  /**
   * 第三方调用 - 模拟失败回调
   */
  reqError() {
    fastman.onReq('IF060002', {
      days: 'aaa',               // 故意传错数据类型
      success : function (resp) {

      },
      fail : function(err) {
    
      },
    })
  }

  navigateNativeTo() {
    var url = this.state.url2
    if (url == null || url == ''){
      log('请填写正确的schema url')
    } else {
      if (window.localStorage) {
        var storage = window.localStorage
        storage['url2'] = url
      }

      fastman.onNavigateNativeTo({
        url: url,
      })
    }
  }

  /**
   * 设置导航头
   */
  navigateBar() {
    const _this = this;
    fastman.onNavigateBar({
      title: '我的标题',                   // 导航标题
      rightButtonTitle: '点我',           // 右侧按钮文字
      onRightButtonPress: function(){    // 右侧按钮点击事件，不定义则不会触发点击
          log('右侧按钮被点击了')
          _this.setState({
            clickedText: '点了 ^_^',
          })
      },
      isShowBackButton: true,            // 是否显示后退按钮，默认true(即显示)
    })
  }

  payTo() {
    fastman.onNavigateTo({
      title: '微信支付',
      uri: '../../wx-pay.html',
    })
  }

  /*
   * 私有逻辑
   */
  selected(e, type) {
    var _f = this.state.fundList
    var _i = _f.indexOf(type)
    if(_i === -1) {
      _f.push(type)
    } else {
      _f.splice(_i, 1)
    }
    this.setState({
      fundList: _f,
    })
  }

  render() {
    return (
      <div class="container" id="container">
        <div class="page input js_show">
            <div class="page__bd">
                <div style={{textAlign:"center"}}><img src={logo} className="App-logo" alt="logo" /></div>
                <div class="weui-cells__title">React注册桥接并授权</div>
                <div class="weui-cells weui-cells_form">
                    <div class="weui-cell">
                        <div class="weui-cell__hd"><label class="weui-label">token</label></div>
                        <div class="weui-cell__bd">
                            <input id="token" class="weui-input" type="text" value={this.state.token} />
                        </div>
                    </div>
                    <div class="weui-cell">
                        <div class="weui-cell__hd"><label class="weui-label">vtDeviceId</label></div>
                        <div class="weui-cell__bd">
                            <input id="vtDeviceId" class="weui-input" type="text" value={this.state.vtDeviceId} />
                        </div>
                    </div>
                </div>
                <div class="weui-footer">
                  <p class="weui-footer__text">授信更新时间 {this.state.queryTime}</p>
                </div>
                <div class="weui-btn-area">
                    <a id="btn1" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.oauth}>oauth授信</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">登录</div>
                <div class="weui-btn-area">
                    <a id="btn2" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.login}>唤起登录</a>
                </div>
                <div class="weui-cells weui-cells_checkbox">
                  <label class="weui-cell weui-check__label" for="s11">
                    <div class="weui-cell__hd">
                      <input type="checkbox" class="weui-check" name="checkbox1" id="s11" checked={this.state.fundList.indexOf('3')!==-1} onChange={(e)=>this.selected(e, '3')} />
                      <i class="weui-icon-checked"></i>
                    </div>
                    <div class="weui-cell__bd">
                      <p>普通资金账号</p>
                    </div>
                  </label>
                  <label class="weui-cell weui-check__label" for="s12">
                    <div class="weui-cell__hd">
                      <input type="checkbox" name="checkbox1" class="weui-check" id="s12" onChange={(e)=>this.selected(e, '6')} />
                      <i class="weui-icon-checked"></i>
                    </div>
                    <div class="weui-cell__bd">
                      <p>两融账号</p>
                    </div>
                  </label>
                  <label class="weui-cell weui-check__label" for="s13">
                    <div class="weui-cell__hd">
                      <input type="checkbox" name="checkbox1" class="weui-check" id="s13" onChange={(e)=>this.selected(e, '18')} />
                      <i class="weui-icon-checked"></i>
                    </div>
                    <div class="weui-cell__bd">
                      <p>期权账号</p>
                    </div>
                  </label>
                </div>
                <div class="weui-btn-area">
                    <a id="btn14" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.loginEnhance}>唤起加强版登录</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">分享</div>
                <div class="weui-btn-area">
                    <a id="btn3" class="weui-btn weui-btn_primary" href="javascript:" onClick={() => this.share('2')}>微信朋友圈分享</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn4" class="weui-btn weui-btn_primary" href="javascript:" onClick={() => this.share('1')}>微信好友分享</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn5" class="weui-btn weui-btn_primary" href="javascript:" onClick={() => this.share('4')}>QQ好友分享</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn6" class="weui-btn weui-btn_primary" href="javascript:" onClick={() => this.share('3')}>微博分享</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">导航</div>
                <div class="weui-cell">
                    <div class="weui-cell__hd"><label class="weui-label">H5 URL</label></div>
                    <div class="weui-cell__bd">
                        <input id="url" class="weui-input" type="text" placeholder="请输入H5 URL" value={this.state.url} onChange={(e)=>this.setState({ url: e.target.value, })} />
                    </div>
                    <div id="btnClearText" class="weui-cell__ft" onClick={()=>this.setState({ url: '', })}>
                        <i class="weui-icon-clear"></i>
                    </div>
                </div>
                <div class="weui-btn-area">
                    <a id="btn7" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.navigateTo}>前进</a>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__hd"><label class="weui-label">原生 URL</label></div>
                    <div class="weui-cell__bd">
                        <input id="url2" class="weui-input" type="text" placeholder="请输入原生 URL" value={this.state.url2} onChange={(e)=>this.setState({ url2: e.target.value, })} />
                    </div>
                    <div id="btnClearText2" class="weui-cell__ft" onClick={()=>this.setState({ url2: '', })}>
                        <i class="weui-icon-clear"></i>
                    </div>
                </div>
                <div class="weui-btn-area">
                    <a id="btn11" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.navigateNativeTo}>前进</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn8" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.navigateBack}>后退</a>
                </div>
            </div>


            <div class="page__bd">
                <div class="weui-cells__title">第三方调用</div>
                <div class="weui-btn-area">
                    <a id="btn9" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.reqOK}>模拟成功调用</a>
                </div>
                <div class="weui-btn-area">
                    <a id="btn10" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.reqError}>模拟失败调用</a>
                </div>
            </div>

            <div class="page__bd">
                <div class="weui-cells__title">设置导航头</div>
                <div class="weui-footer">
                  <p class="weui-footer__text">{this.state.clickedText}</p>
                </div>
                <div class="weui-btn-area">
                    <a id="btn12" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.navigateBar}>设置</a>
                </div>
            </div>

            <div class="page__bd">
                <div class="weui-cells__title">微信支付</div>
                <div class="weui-btn-area">
                    <a id="btn13" class="weui-btn weui-btn_primary" href="javascript:" onClick={this.payTo}>测试支付</a>
                </div>
            </div>

        </div>
        <div class="weui-footer">
          <p class="weui-footer__text">Copyright © 2019-2025 orientsec.ued</p>
        </div>
    </div>
    );
  }
}

export default App;
